<template>
  <div class="text-center mt-3">
    <span class="fs-1 fw-bold text-white bg-brown opacity-75 px-5"
      >最新消息</span
    >
  </div>
  <div class="container my-3">
    <div class="row mt-2 border shadow" v-for="item in articles">
      <div class="col-md-3">
        <img :src="item.image" class="img-fluid" />
      </div>
      <div class="col-md-8">
        <h4 class="mt-4">{{ item.title }}</h4>
        <p class="fs-5 text-danger">
            活動時間 : {{ item.description }}
          </p>
          <p class="text-muted">
            發布日期 :{{ $filters.date(item.create_at) }}
          </p>
        <div class="text-end mb-3">
          <button class="btn btn-brown" @click="getArticleId(item.id)">
            詳細內容
          </button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      articles: [],
      articleId: "",
    };
  },
  methods: {
    getArticles(page=1) {
      this.isLoading = true;
      const api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/articles?page=${page}`;
      this.$http.get(api).then((res) => {
        if (res.data.success) {
          this.isLoading = false;
          console.log(res);
          this.articles = res.data.articles;
          this.pagination = res.data.pagination;
          
        }
      });
    },
    getArticleId(id) {
      // console.log(id);
      this.$router.push(`/article/${id}`);
    },
  },
  created() {
    this.getArticles();
  },
};
</script>
